#qr-code {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #F5F5F5;
    border-radius: 20px;
    padding: 20px;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
}

#qr-code .image-container {
    flex: 1;
    display: flex;
    justify-content: center;
}

#qr-code .text-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

#qr-code img {
    height: 65vmin;
    aspect-ratio: 1;
    border-radius: 8px;
}

.heading {
    font-size: 30px;
    margin-bottom: 10px;
}

.sub-heading {
    font-size: 20px;
    margin-bottom: 20px;
}

@media screen and (max-width: 1024px) {
    #qr-code {
        flex-direction: column;
    }
}
