#blogs {
    max-width: 900px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    row-gap: 35px;
    column-gap: 35px;
    padding: 35px;
    background-color: #fff;
    font-family: 'Circular', sans-serif;
  }

  .blog-card {
    width: calc(calc(100% - 105px) / 4);
    margin: 10px;
    padding: 20px;
    border: 1px solid #eee;
    border-radius: 24px;
    overflow: hidden;
    background-color: #fff;
    font-size: 16px;
    color: #000;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
  }
  
  .blog-card:hover {
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
  }
  
  .blog-card img {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  .blog-card .details {
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    row-gap: 8px;
  }
  
  .blog-card .details h2 {
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 5px;
  }
  
  .blog-card .details p {
    font-size: 14px;
    margin: 0;
  }
  
  .blog-card .details button {
    background-color: #0077FF;
    border: none;
    border-radius: 4px;
    color: #fff;
    padding: 10px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.2s ease-in-out;
  }
  
  .blog-card .details button:hover {
    background-color: #005AE0;
  }
  
  @media screen and (max-width: 768px) {
    .blog-card {
      width: 100%;
      margin: 0 0 20px 0;
    }
  }
  
  @media screen and (max-width: 425px) {
    #blogs {
      width: 100%;
      padding: 10px;
    }
  }
  