body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 18px;
    line-height: 1.5;
    color: #1a1a1a;
    background-color: #f6f6f6;
  }
  
  #full-blog {
    max-width: 680px;
    margin: 0 auto;
    padding: 48px 32px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.04);
    background-color: #fff;
    border-radius: 4px;
  }
  
  #full-blog img {
    max-width: 100%;
    margin: 0 auto;
    display: block;
    border-radius: 4px;
  }
  
  #full-blog h1,
  #full-blog h2,
  #full-blog h3,
  #full-blog h4,
  #full-blog h5 {
    color: #1a1a1a;
    font-weight: 700;
    line-height: 1.3;
    margin-top: 1.5em;
    margin-bottom: 0.5em;
  }
  
  #full-blog h1 {
    font-size: 36px;
  }
  
  #full-blog h2 {
    font-size: 30px;
  }
  
  #full-blog h3 {
    font-size: 24px;
  }
  
  #full-blog h4 {
    font-size: 20px;
  }
  
  #full-blog p {
    font-size: 18px;
    line-height: 1.6;
    margin-top: 1.25em;
    margin-bottom: 1.25em;
  }
  
  #full-blog blockquote {
    margin: 1.5em 0;
    padding: 0 1.5em;
    border-left: 3px solid #dfe2e5;
    color: #6a737d;
    font-style: italic;
  }
  
  #full-blog pre {
    font-size: 16px;
    line-height: 1.45;
    margin-top: 1.5em;
    margin-bottom: 1.5em;
    overflow: auto;
    padding: 1em;
    border-radius: 3px;
    font-family: SFMono-Regular,Consolas,Liberation Mono,Menlo,monospace;
  }
  
  #full-blog code {
    font-size: 85%;
    background-color: rgba(27, 31, 35, 0.05);
    border-radius: 3px;
    padding: 0.2em 0.4em;
    font-family: SFMono-Regular,Consolas,Liberation Mono,Menlo,monospace;
  }
  
  #full-blog a {
    color: #4078c0;
    text-decoration: none;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
  }
  
  #full-blog a:hover {
    color: #4078c0;
    text-decoration: underline;
  }
  
  #full-blog ul,
  #full-blog ol {
    margin-top: 1.25em;
    margin-bottom: 1.25em;
  }
  
  #full-blog li {
    font-size: 18px;
    line-height: 1.6;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
  }
  
  #full-blog table {
    display: table;
    width: 100%;
    overflow: auto;
    word-break: normal;
  }  