.policy-page {
    padding: 60px;
    line-height: 2rem;
}

.policy-page>div {
    margin-bottom: 30px;
}

.policy-page>div>* {
    margin-bottom: 16px;
}

.policy-page table,
.policy-page th,
.policy-page td {
    border-collapse: collapse;
    border: solid 1px;
}

.policy-page td,
.policy-page th {
    padding: 8px;
    margin: 0;
}

.policy-page ul,
.policy-page ol {
    padding-left: 16px;
}

.policy-page a:link,
.policy-page a:visited {
    color: #3B7BD3;
    text-decoration: none;
}

@media screen and (max-width: 768px) {
    .policy-page {
        padding: 30px;
    }
}